<template>
<div class="scrollBox">
  <header-nav :msg="msg" @headerShare="headerShare"></header-nav>
  <div id="browser" class="hasHeader" v-if="article.url">
    <div class="iframe">
      <iframe ref="iframe" frameborder="0" scrolling="no" :src="article.url"></iframe>
    </div>
  </div>
  <div class="share-group f24" :class="{isIos: $utils.isIos()}">
    <div class="sharebutton" @click="toPage('/AwardFeedback')">用户反馈</div>
    <div class="sharebutton active" @click="setShare()">{{download ? '下载掘金宝APP' : '分享好友（可锁粉）'}}</div>
  </div>
  <carShare v-if="showCarShare" @shareSuccess='shareSuccess' :appShare='appShare' :fatherParams='fatherParams'></carShare>
  <share-box v-if="showBox" @shareAction ="shareActions" @shareSuccess="shareSuccess"></share-box>
  <ShareCommon @closeShare='closeShare' @getArc='getArc' v-if="showConfirm"></ShareCommon>
</div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import carShare from '@/components/share/carShare'
import shareBox from '@/components/common/share_box'
import ShareCommon from '@/components/share/shareCommon'
import {mapMutations} from 'vuex'
export default {
  components: {
    HeaderNav,
    carShare,
    shareBox,
    ShareCommon
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      msg: '',
      download: false,
      showCarShare: false,
      showBox: false,
      current: null,
      appShare: {
        count: 0
      },
      showConfirm: false,
      article: {
        
      },
      fatherParams: {}
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    toPage (url) {
      this.$router.push(url)
    },
    shareActions (actions) {
      let msg = {
        title: this.fatherParams.title,
        content: this.fatherParams.desc,
        thumbs: [this.fatherParams.img_url],
        href: this.fatherParams.shareLink
      }
      let id = 'qq'
      if (actions === 'cancel') {
        this.showBox = false
        return
      } else if (actions === 'friend') {
        id = 'weixin'
        msg.type = 'web'
        msg.extra = {
          scene: 'WXSceneTimeline'
        }
      } else if (actions === 'wechat') {
        id = 'weixin'
        msg.type = 'web'
        msg.extra = {
          scene: 'WXSceneSession'
        }
      } else if (actions === 'qq') {
        id = 'qq'
      }
      console.log(msg)
      this.commonState.shares[id].send(msg, () => {
        this.showBox = false
        if (id === 'qq') {
          this.$toast({
            message: '分享成功',
            duration: 1500
          })
        }
      }, (err) => {
        this.showBox = false
        if (err.code === -2) {
          this.$toast({
            message: '取消分享',
            duration: 1500
          })
        } else {
          this.$toast({
            message: '分享失败',
            duration: 1500
          })
        }
      })
    },
    closeShare () {
      this.showConfirm = false
    },
    setShare () {
      if (this.$config.source_style >= 6) {
        this.headerShare()
      } else if (this.$utils.isWx()) {
        this.showConfirm = true
      } else {
        this.$utils.downLoadExtra()
      }
    },
    headerShare () {
      this.showBox = true
    },
    shareSuccess () {
      this.showBox = false
    },
    getContent () {
      let data = {
        aid: this.$route.params.id
      }
      this.$api.get('DETAIL', data, res => {
        this.article = {
          title: res.data.title,
          desc: res.data.describe,
          img_url: res.data.img_url.length > 0 ? res.data.img_url[0] : 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/share_logo.png',
          url: res.data.url
        }
        this.appShare.path = this.$route.path
        this.showCarShare = true
        this.fatherParams = {
          title: this.article.title,
          desc: this.article.desc,
          img_url: this.article.img_url ? this.article.img_url : 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/share_logo.png'
        }
        document.title = this.$utils.splitStr(this.fatherParams.title, 14)
        this.msg = document.title
        let params = {
          path: this.$route.path,
          inviteCode: this.commonState.userInfo.invitation
        }
        if (localStorage.getItem('domain')) {
          this.$config.currentWebUrl = localStorage.getItem('domain')
        }
        if (this.$config.source_style >= 6) {
          this.shareLink = `${this.$config.currentWebUrl}?${this.$qs.stringify(params)}`
        } else {
          this.shareLink = `${location.origin}?${this.$qs.stringify(params)}`
        }
        this.fatherParams.shareLink = this.shareLink
        this.showCarShare = true
      })
    }
  },
  created () {
    this.getContent()
    if (this.$config.source_style >= 6) {
      this.download = false
    } else if (this.$utils.isWx()) {
      if (this.$route.query.inviteCode) {
        this.download = true
      } else {
        this.download = false
      }
    } else {
      this.download = true
    }
  },
  mounted () {
    
  }
}
</script>
<style lang="scss" scoped>
.share-group {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  box-sizing: border-box;
  align-items: center;
  height: 100px;
  border-top: 1px solid #eee;
  background: #fff;
  font-size: 28px;
  &.isIos {
    .sharebutton {
      padding-top: 20px;
    }
  }
  .sharebutton {
    text-align: center;
    padding: 22px 0 14px;
    line-height: 34px;
    color: #FF7F18;
    border: 1px solid #FF7F18;
    width: 240px;
    line-height: 40px;
    height: 80px;
    box-sizing: border-box;
    border-radius: 70px;
    &.active {
      background: #F38D32;
      color: #fff;
      width: 360px;
    }
  }
}
#browser {
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
  .hasHeader {
    .iframe {
      height: calc(100vh - 88px);
    }
  }
  .iframe {
    width: 100%;
    height: 100%;
    overflow: auto!important;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    padding-bottom: 100px;
  }
  iframe {
    display: block;
    width: 100%;
    // height: 200000px;
    // user-select: none;
  }
}
</style>



